.gifCard {
  width: 200px;
  height: 160px;
  padding: 5px;

  position: relative;
  top: 0;
  -webkit-transition: top ease 0.5s;
  -o-transition: top ease 0.5s;
  transition: top ease 0.5s;

  cursor: pointer;
}

.gifCard:hover {
  top: -2px;
}



